.animated {
	animation-duration: $transition-duration-1;
	animation-fill-mode: both;
}

@keyframes fadeInDown {

	from {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}

	to {
		opacity: 1;
		transform: translate3d(0, 0, 0);
	}
}

.fade-in-down {
	animation-name: fadeInDown;
}



